        display: none;
    }


    .thumbnail_sp img, .thumbnail_pc img {
        width: 100%;
    }

    .work_thumbnail p {
        text-align: right;
    }

    .work_thumbnail p img {
        height: 32px;
        padding: 16px 4px;
        filter: brightness(1);
    }

    .work_thumbnail a {
        display: block;
        text-align: center;
        padding: 8px 0;
    }

    .work_thumbnail a span {
        display: block;
        padding: 16px 0;
        font-size: 1.3rem;
