    }

    .work_detail h4 {
        margin: 8px 0 24px;
        font-weight: normal;
        font-size: 1.2rem;
        color: #797979;
    }

    .work_detail h4 u {
        color: #dc143c;
    }

    .work_detail p {
        display: inline-block;
        width: 100%;
        vertical-align: top;
        font-size: 1.1rem;
        margin-bottom: 1rem;
    }

    .work_detail a {
        color: white;
    }


}
